.l-panel {
    min-height: 56px;
    border-bottom: 1px solid #ddd;
    margin-bottom: 15px;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    background: #fff;
    z-index: 100;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    z-index: 100;
}

.android .l-panel {
   /* padding-top: 25px*/
}

.ios .l-panel {
    border-top: var( --safe-area-inset-top ) solid transparent;
    /*padding-top: 25px;*/
}

.c-panel__back {
    font-size: 20px;
    cursor: default;
    vertical-align: middle;
    display: inline-block;
    width: 50px;
    padding: 10px;
    text-align: center;
}

.web .c-panel__back {
    cursor: pointer;
}

.web .c-panel__back:hover {
    background: rgba( 0, 0, 0, 0.05 );
    border-radius: 5px;
}

.web.theme-dark .c-panel__back:hover {
    background: rgba( 255, 255, 255, 0.05 );
}

.c-panel__title {
    width: calc( 100% - 180px );
    display: inline-block;
    vertical-align: middle;
    font-size: 18px;
    font-family: 'Arial Rounded MT PRO Cyr';
    padding-top: 20px;
    padding-bottom: 15px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.c-panel__title--center {
    width: 100%;
    max-width: 100%;
    text-align: center;
}


.theme-dark .l-panel {
    background: #262626;
    border-bottom: 1px solid #333;
}