.c-switch {
	user-select: none;
	margin: 10px 0;
}

.c-switch:after {
	content: '';
	display: block;
	clear: both;
}

.c-switch__label {
	display: block;
	user-select: none;
	cursor: pointer;
}

.c-switch__checkbox {
	display: none;
}


.c-switch__text {
	padding: 2px 0;
	cursor: pointer;
	user-select: none;
	display: inline-block;
	max-width: calc( 100% - 50px );
}


.c-switch__switch-label {
	width: 40px;
	height: 25px;
	background: #f6f6f6;
	display: inline-block;
	vertical-align: middle;
	border: 1px solid #ddd;
	border-radius: 20px;
	position: relative;
	transition: 0.15s ease-in-out;
	cursor: pointer;
	float: right;
}

.c-switch__checkbox + .c-switch__switch-label:after {
	content: '';
	width: 19px;
	height: 19px;
	background: #ddd;
	display: inline-block;
	border-radius: 50%;
	position: absolute;
	top: 2px;
	left: 2px;
	transition: 0.15s ease-in-out;
	border: 1px solid #aaa;
}

.c-switch__checkbox:checked + .c-switch__switch-label {
	background: #262626;
	border: 1px solid #111;
}

.c-switch__checkbox:checked + .c-switch__switch-label:after {
	left: calc( 100% - 23px );
	background: #fff;
	border: 1px solid #f6f6f6;
}

.web .c-switch:hover .c-switch__switch-label {
	border: 1px solid #aaa;
}

.web .c-switch:hover .c-switch__checkbox:checked + .c-switch__switch-label {
	border: 1px solid #262626;
}


.theme-dark .c-button--with-img-icon {
    
}

.theme-dark .c-input__text {
    background: #262626;
    border: 1px solid #333;
    color: inherit;
}

.theme-dark .c-input__text:focus,
.theme-dark .c-select__select:focus {
    border: 1px solid #fff;
}

.theme-dark .c-input__placeholder {
    background: #262626;
    color: #fff !important;
}

.theme-dark .c-switch__switch-label {
	background: #1d1d1d;
    border: 1px solid #555;
}


.theme-dark .c-switch__checkbox + .c-switch__switch-label:after {
	background: #333;
    border: 1px solid #555;
}

.theme-dark .c-select__select {
	color: #fff;
	border: 1px solid #555;
}

.theme-dark .c-select__select option {
	color: #262626;
}

.theme-dark .c-switch__checkbox:checked + .c-switch__switch-label {
	background: #ffffff;
    border: 1px solid #fff;
}

.theme-dark .c-switch__checkbox:checked + .c-switch__switch-label:after {
    background: #262626;
    border: 1px solid #111;
}