#notice {
    position: fixed;
    bottom: calc( 10px + var(--safe-area-inset-bottom) );
    left: 10px;
    width: calc( 100% - 20px );
    padding: 15px 20px;
    padding-right: 50px;
    background: #111;
    color: #fff;
    border: 1px solid #333;
    border-radius: 10px;
    box-sizing: border-box;
    animation: popup_up 0.15s ease-in-out;
    z-index: 999;
    display: none;
}

.notice--hidden {
    display: none !important;
}

.notice--active {
    display: block !important;
}

#notice.notice_hide {
    animation: notice_hide 0.15s ease-in-out forwards;
}

.notice_close {
    display: inline-block;
    position: absolute;
    top: 8px;
    right: 10px;
    width: 32px;
    height: 32px;
}


.notice_close:after,
.notice_close:before {
    content: '';
    display: inline-block;
    background: #555;
    position: absolute;
    transform: rotateZ( -45deg );
}

.notice_close:after {
    width: 2px;
    height: 16px;
    margin-left: -1px;
    left: 50%;
    top: 8px;
}

.notice_close:before {
    width: 16px;
    height: 2px;
    margin-top: -1px;
    top: 50%;
    left: 8px;
}

.web .notice_close:hover {
    cursor: pointer;
}

.web .notice_close:hover:after,
.web .notice_close:hover:before {
    background: #aaa;
}

@keyframes notice_hide {
    from {
        display: block !important;
        opacity: 1;
    }

    to {
        transform: translateY( 50px );
        opacity: 0;
        display: none;
    }
}

@keyframes popup_up {
    from {
        transform: translateY( 50px );
        opacity: 0;
    }
}