.player {
	/*position: fixed;*/
    position: relative;
    background: #fff;
    width: 100%;
    box-sizing: border-box;
    border-top: 1px solid #ddd;
    padding: 0px 0px;
}

.ios .player {
    border-bottom: var( --safe-area-inset-bottom ) solid transparent;
    position: sticky;
    position: -webkit-sticky;
}

.theme-dark .player {
	background: #262626;
	border-top: 1px solid #333;
}

.player .c-category__text {
    width: 100%;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}


.Tabbar {
    height: 58px;
    box-shadow: none;
}

.theme-dark .Tabbar {
    background: #262626;
}

.Tabbar:before {
    display: none;
}