#player_preloader {
    position: fixed;
    bottom: 12px;
    left: 20px;
	width: 34px;
	height: 34px;
	box-sizing: border-box;
    display: none;
    animation: fade_out 0.15s ease-in-out forwards;
}

#player_preloader.player-preloader--active {
    display: block;
    animation: fade_in 0.15s ease-in-out;
}

.player-preloader--fill {
    background: #fff !important;
}

.theme-dark .player-preloader--fill {
    background: #262626;
}

#player_preloader:after {
    content: '';
    display: block;
    width: 16px;
    height: 16px;
    box-sizing: border-box;
    border: 2px solid #fff;
    border-top: 2px solid #000;
    border-right: 2px solid #000;
    border-radius: 50%;
    margin: -8px;
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 100;
    animation: spinner 1s linear infinite;
}

.theme-dark #player_preloader{
   
}

.theme-dark #player_preloader:after {
    border-top: 2px solid #000;
    border-right: 2px solid #000;
}

.preloader--active ~ #player_preloader {
	display: none;
}

@keyframes spinner {
    to {
        transform: rotateZ( 360deg );
    }
}

@keyframes fade_in {
    from {
        opacity: 1;
    }
}

@keyframes fade_out {
    to {
        opacity: 0;
    }
}