.c-player__control {
    display: inline-block;
    vertical-align: middle;
    font-size: 40px;
    text-align: center;
    padding: 10px 5px;
    box-sizing: border-box;
    cursor: default;
    border-radius: 5px;
    height: 100%;
    min-width: 30px;
}

.c-player__control--10 {
	width: calc( 10% - 5px );
}

.c-player__control--15 {
	width: calc( 15% - 5px );
}

.c-player__control--20 {
	width: calc( 20% - 5px );
}

.c-player__control--25 {
	width: calc( 25% - 5px );
}

.c-player__control--30 {
	width: calc( 30% - 5px );
}

.c-player__control--small-icon {
	font-size: 20px;
	color: #888;
}

.c-player__control:active,
.web .c-player__control:hover {
    background: rgba( 0, 0, 0, 0.05 );
}

.web .c-player__control {
    cursor: pointer;
}