.c-menu-buttons__item {
	margin: 0;
	border-radius: 5px;
	padding: 8px;
	box-sizing: border-box;
	user-select: none;
}

.c-menu-buttons__item:active,
.web .c-menu-buttons__item:hover {
	background: rgba( 0, 0, 0, 0.05 );
}

.web .c-menu-buttons__item:hover {
	cursor: pointer;
}

.theme-dark.web .c-menu-buttons__item:hover
.theme-dark .c-menu-buttons__item:active {
	background: rgba( 255, 255, 255, 0.05 );
}

.c-menu-buttons__item:active {
	transform: scale( 0.95 );
}

.theme-dark .c-menu-buttons__icon {
	/*color: #fff;*/
}

.c-menu-buttons__icon {
	display: inline-block;
	vertical-align: middle;
	margin-right: 5px;
	font-size: 24px;
	color: inherit;
}

.c-menu-buttons__icon--outlined {
	font-family: 'Material Icons Outlined';
}

.c-menu-buttons__text {
	display: inline-block;
	vertical-align: middle;
	width: calc( 100% - 40px );
	box-sizing: border-box;
	padding-left: 8px;
	font-family: 'Arial Rounded MT Pro Cyr';
	color: inherit;
}

.c-menu-buttons__item--destructive {
	color: #bd1c1c;
}