.c-input-select {
	position: relative;
}

.c-select {
	display: inline-block;
	position: relative;
	vertical-align: top;
	width: 100%;
}

/*hide shadow in ios*/
/*hide arrow*/

.c-select__select {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
}

.c-select__select {
	padding: 8px 15px;
	border: 1px solid #ddd;
	border-radius: 4px;
	font-size: 16px;
	outline: none;
	font-weight: 300;
	box-sizing: border-box;
	margin: 5px 0;
	width: 100%;
	font-family: inherit;
}

.c-select__select:disabled {
	background: #f5f5f5;
}

.c-select__select:disabled:hover {
	cursor: not-allowed;
}

.theme-dark .c-select__select:disabled {
	background: #333;
	color: #888;
} 

.c-input-select__dropdown-icon {
    position: absolute;
    color: #aaa;
    right: 15px;
    top: 12px;
    pointer-events: none;
}

.c-select__select {
	cursor: pointer;
	background: none;
}

.c-select__select:focus {
	border: 1px solid #262626;
}

.c-select__options {
	width: 100%;
	padding: 5px 0;
	border: 1px solid #262626;
	border-radius: 5px;
	box-sizing: border-box;
	top: calc( 100% + 5px );
	left: 0;
	position: absolute;
	z-index: 100;
	background: #fff;
	box-shadow: 0px 4px 4px rgba( 0, 0, 0, 0.3 );
	display: none;
}

.c-select__options--above {
	top: auto;
	bottom: calc( 100% + 5px );
}

.c-select__option {
	padding: 5px 15px;
	cursor: pointer;
}

.c-select__option:hover {
	background: rgba( 0, 0, 0, 0.05 );
}

.theme-dark option {
	background: #262626;
	color: #fff !important;
}