#offline {
    background: #bd1c1c;
    color: #fff;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 5px;
    border-bottom: var(--safe-area-inset-bottom) solid transparent;
    box-sizing: border-box;
    text-align: center;
    display: none;
    z-index: 900;
    font-weight: 400;
    font-size: 14px;
    animation: popup_up 0.15s ease-in-out;
}

#offline:after {
    content: '';
    display: inline-block;
    vertical-align: middle;
    width: 14px;
    height: 14px;
    border: 2px solid transparent;
    border-left: 2px solid #fff;
    border-top: 2px solid #fff;
    border-radius: 50%;
    box-sizing: border-box;
    margin-left: 5px;
    animation: spinner 1s linear infinite;
}

.offline--active {
	display: block !important;
}

@keyframes spinner {
    to {
        transform: rotateZ( 360deg );
    }
}

@keyframes popup_up {
    from {
        transform: translateY( 50px );
        opacity: 0;
    }
}