.c-player-layout-wrap {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	justify-content: center;
	align-items: center;
	height: calc( 100vh - 68px );
	overflow: hidden;
	-webkit-overflow-scrolling: auto;
}

.android .c-player-layout-wrap {
	height: calc( 100vh - 105px );
}

.c-player-layout {
    text-align: center;
    position: relative;
    height: 100%;
    max-height: 600px;
    width: 100%;
}