.c-player__cover {
    border-radius: 10px;
    background-size: contain !important;
    background-position: center !important;
    background-repeat: no-repeat !important;
    width: 300px;
    height: 300px;
    max-width: 100%;
    margin: 0 auto;
    margin-bottom: 20px;
    background: #ddd;
    position: relative;
    
    max-height: calc( 100vh - 68px - 230px );
}

.c-player__cover--animate {
    animation: player_cover 0.2s ease-in-out;
}

.c-player__preloader {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 20px;
    height: 20px;
    border: 2px solid #fff;
    border-top: 2px solid #000;
    border-left: 2px solid #000;
    border-radius: 50%;
    box-sizing: border-box;
    margin: -10px;
    animation: spinner 1s linear infinite;
}

.c-player__preloader--active {
    display: block;
}

@keyframes spinner {
    to {
        transform: rotateZ( 360deg );
    }
}

@keyframes player_cover {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}