.c-category {
    width: 100%;
    padding: 0px 20px;
    /*border: 1px solid #ddd;*/
    border-radius: 10px;
    box-sizing: border-box;
    margin-bottom: 5px;
    display: block;
    color: inherit;
    cursor: default;
    text-decoration: none;
    position: relative;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.c-category:active,
.web .c-category:hover {
    background: rgba( 0, 0, 0, 0.05 );
}

.c-category:active .c-category__icon--additional,
.web .c-category:hover .c-category__icon--additional {
    background: #f2f2f2;
}

.theme-dark .c-category:active .c-category__icon--additional,
.theme-dark.web .c-category:hover .c-category__icon--additional {
    background: #242424;
}

.web .c-category {
    cursor: pointer;
}


.c-category__text-content {
    display: inline-block;
    vertical-align: middle;
    box-sizing: border-box;
    width: calc( 100% - 50px );
}

.c-category__icon {
    display: inline-block;
    vertical-align: middle;
    font-size: 34px;
    margin-right: 15px;
}

.c-category__icon--additional {
    position: absolute;
    font-size: 12px;
    padding: 1px;
    background: #fff;
    border-radius: 50% 0 0 0;
    left: 43px;
    top: calc( 50% + 5px );
}

.c-category__icon--additional:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 10px;
    height: 10px;
    border-radius: 5px 0 0 0;
    border: 1px solid transparent;
    transition: 0.15s linear;
    border-top: 1px solid rgba( 0, 0, 0, 0.05 );
    border-left: 1px solid rgba( 0, 0, 0, 0.05 );
}

.c-category__icon--additional:empty {
    display: none;
}

.theme-dark .c-category__icon--additional {
    background: #262626;
}

.theme-dark .c-category__icon--black {
    color: #fff;
}

.theme-dark .c-category__icon--brown {
    color: #c54d1e;
}

.theme-dark .c-category__icon--purple {
    color: #a581e4;
}

.theme-dark .c-category__icon--red {
    color: #f74747;
}

.c-category__icon--image {
    width: 34px;
    height: 34px;
    border-radius: 5px;
    background-repeat: no-repeat !important;
    background-size: contain !important;
    background-position: center !important;
    position: relative;
}

.c-category__icon--image:after {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 5px;
    box-sizing: border-box;
    border: 1px solid rgba( 0, 0, 0, 0.05 );
}

.theme-dark .c-category__icon--image:after {
    border: 1px solid rgba( 255, 255, 255, 0.05 );
}

.theme-dark .c-category__icon--additional:after {
    border-top: 1px solid rgba( 255, 255, 255, 0.05 );
    border-left: 1px solid rgba( 255, 255, 255, 0.05 );
}

.c-category__icon--blue {
    color: #1c82bd;
}

.c-category__icon--green {
    color: #86c44e;
}

.c-category__icon--red {
    color: #bd1c1c;
}

.c-category__icon--black {
    color: #262626;
}

.c-category__icon--orange {
    color: #e6a00a;
}

.c-category__icon--brown {
    color: #652a13;
}

.c-category__icon--purple {
    color: #673ab7;
}

.c-category__icon--grey {
    color: #888;
}


.c-category__icon--filled {
    color: #fff;
    font-size: 24px;
    padding: 5px;
    margin: -5px;
    border-radius: 50%;
    margin-right: 15px;
}

.c-category__icon--filled.c-category__icon--blue {
    background-color: #08AEEA;
    background-image: linear-gradient(315deg, #08AEEA 0%, #2af5f2 100%);
}

.c-category__icon--filled.c-category__icon--red {
    background-color: #ff9a9a;
    background-image: linear-gradient(135deg, #ff9a9a 0%, #bd1c1c 100%);
}

.c-category__icon--filled.c-category__icon--green {
    background-color: #85FFBD;
    background-image: linear-gradient(134deg, #85FFBD 0%, #86c44e 100%);
}

.c-category__icon--filled.c-category__icon--orange {
    background-color: #FBAB7E;
    background-image: linear-gradient(315deg, #FBAB7E 0%, #F7CE68 100%);
}

.c-category__icon--filled.c-category__icon--purple {
    background-color: #8BC6EC;
    background-image: linear-gradient(135deg, #8BC6EC 0%, #673ab7 100%);
}

.c-category__icon--filled.c-category__icon--brown {
    background-color: #c1785b;
    background-image: linear-gradient(134deg, #c1785b 0%, #652a13 100%);
}

.c-category__icon--filled.c-category__icon--black {
    background-color: #444444;
    background-image: linear-gradient(134deg, #444444 0%, #262626 100%);
}

.c-category--nopad {
    padding: 10px 0;
}

.c-category--small-pad {
    padding: 0 10px;
}

.c-category--small-pad .c-category__icon--additional {
    left: 30px;
}

.c-category--nopad:hover {
    background: rgba( 0, 0, 0, 0 );
}

.c-category__icon--text {
    font-family: 'Arial Rounded MT Pro Cyr';
    font-size: 16px;
    max-width: 30px;
}


.c-category--compact {
    display: inline-block;
    vertical-align: middle;
    width: auto;
    text-align: center;
    padding: 10px;
    width: 25%;
}

.c-category--compact .c-category__icon {
    margin: 0;
    font-size: 26px;
    line-height: 30px;
    margin-bottom: 5px;
}

.c-category--compact .c-category__text {
    display: none;
}

.c-category--compact .c-category__title {
    font-size: 12px;
    width: 100%;
    text-overflow: ellipsis;
    overflow: hidden;
}

.c-category--compact .c-category__text-content {
    display: block;
    width: 100%;
}




.c-category__title {
    font-family: 'Arial Rounded MT Pro Cyr';
}

.c-category__text {
    font-size: 14px;
    font-weight: 400;
    word-wrap: break-word;
    word-break: normal; /*  Opera  FireFox */
    word-break: break-word; /*  Chrome */
}

.c-category--with-button {
    position: relative;
}

.c-category__button {
    display: inline-block;
    position: absolute;
    top: 50%;
    right: 10px;
    padding: 5px;
    margin-top: -17px;
    color: #aaa;
    border-radius: 5px;
}

.c-category__button:active,
.web .c-category__button:hover {
    background: rgba( 0, 0, 0, 0.05 );
}

.c-category--with-button .c-category__title {
    width: 100%;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.c-category--with-button .c-category__text-content {
    /*padding-right: 30px;*/
    text-decoration: none;
    display: inline-block;
    color: inherit;
}

.c-category__date {
    font-size: 14px;
    color: #888;
}

.c-category__click-area {
	display: inline-block;
	width: 100%;
    padding: 10px 0;
}

.c-category--with-button .c-category__click-area {
	width: calc( 100% - 30px )
}