@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,500&subset=cyrillic');

@font-face {
    font-family: 'Arial Rounded MT Pro Cyr';
    src: url('fonts/ArialRoundedMTProCyr-Bold.eot');
    src: url('fonts/ArialRoundedMTProCyr-Bold.eot?#iefix') format('embedded-opentype'),
        url('fonts/ArialRoundedMTProCyr-Bold.woff2') format('woff2'),
        url('fonts/ArialRoundedMTProCyr-Bold.woff') format('woff'),
        url('fonts/ArialRoundedMTProCyr-Bold.ttf') format('truetype');
    font-weight: bold;
    font-style: normal;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@font-face {
    font-family: 'vk-icons';
    src:  url('fonts/vk-icons.eot?n07opm');
    src:  url('fonts/vk-icons.eot?n07opm#iefix') format('embedded-opentype'),
    url('fonts/vk-icons.ttf?n07opm') format('truetype'),
    url('fonts/vk-icons.woff?n07opm') format('woff'),
    url('fonts/vk-icons.svg?n07opm#vk-icons') format('svg');
    font-weight: normal;
    font-style: normal;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.c-icon--vk {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'vk-icons' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1; 
    /* Enable Ligatures ================ */
    letter-spacing: 0;
    -webkit-font-feature-settings: "liga";
    -moz-font-feature-settings: "liga=1";
    -moz-font-feature-settings: "liga";
    -ms-font-feature-settings: "liga" 1;
    font-feature-settings: "liga";
    -webkit-font-variant-ligatures: discretionary-ligatures;
    font-variant-ligatures: discretionary-ligatures;
    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 24px;

    /*Preventing user selection*/
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
}


.c-button {
	font-size: 16px;
	font-family: 'Arial Rounded MT Pro Cyr';
	padding: 8px 20px;
	margin: 2.5px 0;
	cursor: pointer;
	outline: none;
	color: inherit;
	border-radius: 10px;
	border: none;
	background: none;
	border: 1px solid transparent;
	display: inline-block;
	text-decoration: none;
	text-align: center;
	vertical-align: middle;
	user-select: none;
}

.c-button:active {
	transform: scale( 0.95 );
}

.c-button:active,
.web .c-button:hover {
	background: #f6f6f6;
}

.c-button:disabled {
	background: #f6f6f6;
	color: #888;
	border: 1px solid #ddd;
}

.c-button--wide {
	width: 100%;
	box-sizing: border-box;
}



body {
	font-family: 'Roboto' !important;
	background: #fff !important;
    font-family: 'Roboto' !important;
}

.theme-dark {
    color: #fff !important;
}

.theme-dark #root {
    color: #fff !important;
}

.Panel__in {
	background: #fff !important;
}

.Panel--tm-gray .Panel__in, .Panel--tm-gray::after {
    background: #fff !important;
}

.theme-dark .Panel--tm-gray::after {
    background: #262626 !important;
}

.theme-dark .Panel__in {
    background: #262626 !important;
    color: #fff !important;
}

#AppContainer {
    height: 100%;
    width: 100%;
}

.noscroll {
    overflow: hidden;
}


@supports (padding-top: constant(safe-area-inset-top)) {
  :root {
    --safe-area-inset-top: constant(safe-area-inset-top);
    --safe-area-inset-bottom: constant(safe-area-inset-bottom);
  }
}

@supports (padding-top: env(safe-area-inset-top)) {
  :root {
    --safe-area-inset-top: env(safe-area-inset-top);
    --safe-area-inset-bottom: env(safe-area-inset-bottom);
  }
}


#external_link {
    display: none;
    position: absolute;
    top: -9999px;
    height: -9999px;
}


.theme-dark {
    background: #262626 !important;
}


.ios .hide-item--ios,
.android .hide-item--android,
.mobile_web .hide-item--mobile_web,
.web .hide-item--web {
    display: none;
}