.popup_wrapper {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background: rgba( 0, 0, 0, 0.5 );
    z-index: 900;
    overflow-x: hidden;
    box-sizing: border-box;
    animation: popup_wrapper_show 0.3s ease-in-out;
}

.ios .popup_wrapper {
    position: sticky;
    position: -webkit-sticky;
}

.popup_mask {
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0;
}

.popup_bottom_wrapper {
    display: table-cell;
    vertical-align: bottom;
    height: 100vh;
    width: 100vw;
    overflow: hidden;
    vertical-align: top;
    box-sizing: border-box;
    padding-top: 65px;
}

.popup_bottom .popup_bottom_wrapper {
    vertical-align: bottom;
}

.popup_middle .popup_bottom_wrapper {
    vertical-align: middle;
}

.popup {
    width: calc( 100% - 30px );
    display: block;
    max-width: 400px;
    padding: 30px;
    margin: 15px auto;
    border-radius: 10px;
    box-sizing: border-box;
    background: #fff;
    position: relative;
    animation: popup_down 0.15s ease-in-out;
}

.popup_bottom .popup {
    animation: popup_up 0.15s cubic-bezier(.57,.57,.41,1);
}

.popup_middle .popup {
    animation: popup_up 0.15s ease-in-out;
    margin-top: 15px;
}

.popup_wrapper_hide {
    animation: popup_wrapper_hide 0.15s linear forwards;
}

.popup_wrapper_hide .popup,
.popup_wrapper_hide.popup_middle .popup {
    animation: popup_hide_up 0.35s cubic-bezier(.57,.57,.41,1) forwards;
}

.popup_wrapper_hide.popup_bottom .popup {
    animation: popup_hide_down 0.3s cubic-bezier(.57,.57,.41,1) forwards;
}

.popup_close {
    display: inline-block;
    position: absolute;
    top: 20px;
    right: 20px;
    width: 32px;
    height: 32px;
}

.popup_close:after,
.popup_close:before {
    content: '';
    display: inline-block;
    background: #ddd;
    position: absolute;
    transform: rotateZ( -45deg );
}

.popup_close:after {
    width: 2px;
    height: 16px;
    margin-left: -1px;
    left: 50%;
    top: 8px;
}

.popup_close:before {
    width: 16px;
    height: 2px;
    margin-top: -1px;
    top: 50%;
    left: 8px;
}



@keyframes popup_up {
    from {
        transform: translateY( 100px );
        opacity: 0;
    }
}

@keyframes popup_down {
    from {
        transform: translateY( -50px );
        opacity: 0;
    }
}


@keyframes popup_wrapper_hide {
    to {
        opacity: 0;
        display: none;
    }
}

@keyframes popup_wrapper_show {
    to {
        opacity: 1;
    }
}

@keyframes popup_hide_up {
    to {
        transform: translateY( -50px );
        opacity: 0;
        display: none;
    }
}

@keyframes popup_hide_down {
    to {
        transform: translateY( 150px );
        opacity: 0;
        display: none;
    }
}