.popup {
    width: calc( 100% - 30px );
    display: block;
    max-width: 400px;
    padding: 30px;
    margin: 15px auto;
    border-radius: 10px;
    box-sizing: border-box;
    background: #fff;
    position: relative;
    -webkit-animation: popup_down 0.15s ease-in-out;
            animation: popup_down 0.15s ease-in-out;
}

.popup--animated {
    -webkit-transition: 0.3s cubic-bezier(.57,.57,.41,1);
    transition: 0.3s cubic-bezier(.57,.57,.41,1);
}

.popup_close {
    display: inline-block;
    position: absolute;
    top: 20px;
    right: 20px;
    width: 32px;
    height: 32px;
}

.popup_close:after,
.popup_close:before {
    content: '';
    display: inline-block;
    background: #ddd;
    position: absolute;
    -webkit-transform: rotateZ( -45deg );
            transform: rotateZ( -45deg );
}

.web .popup_close:hover {
    cursor: pointer;
}

.web .popup_close:hover:after,
.web .popup_close:hover:before,
.popup_close:active:after,
.popup_close:active:before {
    background: #aaa;
}

.popup_close:after {
    width: 2px;
    border-radius: 2px;
    height: 16px;
    margin-left: -1px;
    left: 50%;
    top: 8px;
}

.popup_close:before {
    width: 16px;
    height: 2px;
    border-radius: 2px;
    margin-top: -1px;
    top: 50%;
    left: 8px;
}

