.c-switch {
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	margin: 10px 0;
}

.c-switch:after {
	content: '';
	display: block;
	clear: both;
}

.c-switch__label {
	display: block;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	cursor: pointer;
}

.c-switch__checkbox {
	display: none;
}


.c-switch__text {
	padding: 2px 0;
	cursor: pointer;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	display: inline-block;
	max-width: calc( 100% - 50px );
}


.c-switch__switch-label {
	width: 40px;
	height: 25px;
	background: #f6f6f6;
	display: inline-block;
	vertical-align: middle;
	border: 1px solid #ddd;
	border-radius: 20px;
	position: relative;
	-webkit-transition: 0.15s ease-in-out;
	transition: 0.15s ease-in-out;
	cursor: pointer;
	float: right;
}

.c-switch__checkbox + .c-switch__switch-label:after {
	content: '';
	width: 19px;
	height: 19px;
	background: #ddd;
	display: inline-block;
	border-radius: 50%;
	position: absolute;
	top: 2px;
	left: 2px;
	-webkit-transition: 0.15s ease-in-out;
	transition: 0.15s ease-in-out;
	border: 1px solid #aaa;
}

.c-switch__checkbox:checked + .c-switch__switch-label {
	background: #262626;
	border: 1px solid #111;
}

.c-switch__checkbox:checked + .c-switch__switch-label:after {
	left: calc( 100% - 23px );
	background: #fff;
	border: 1px solid #f6f6f6;
}

.web .c-switch:hover .c-switch__switch-label {
	border: 1px solid #aaa;
}

.web .c-switch:hover .c-switch__checkbox:checked + .c-switch__switch-label {
	border: 1px solid #262626;
}


