.theme-dark.web .c-button--secondary:hover {
    background: rgba( 255, 255, 255, 0.05 );
}
.c-player__cover {
    border-radius: 10px;
    background-size: contain !important;
    background-position: center !important;
    background-repeat: no-repeat !important;
    width: 300px;
    height: 300px;
    max-width: 100%;
    margin: 0 auto;
    margin-bottom: 20px;
    background: #ddd;
    position: relative;
    
    max-height: calc( 100vh - 68px - 230px );
}

.c-player__cover--animate {
    -webkit-animation: player_cover 0.2s ease-in-out;
            animation: player_cover 0.2s ease-in-out;
}

.c-player__preloader {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 20px;
    height: 20px;
    border: 2px solid #fff;
    border-top: 2px solid #000;
    border-left: 2px solid #000;
    border-radius: 50%;
    box-sizing: border-box;
    margin: -10px;
    -webkit-animation: spinner 1s linear infinite;
            animation: spinner 1s linear infinite;
}

.c-player__preloader--active {
    display: block;
}

@-webkit-keyframes spinner {
    to {
        -webkit-transform: rotateZ( 360deg );
                transform: rotateZ( 360deg );
    }
}

@keyframes spinner {
    to {
        -webkit-transform: rotateZ( 360deg );
                transform: rotateZ( 360deg );
    }
}

@-webkit-keyframes player_cover {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes player_cover {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}
.qr {
    padding: 20px;
    border-radius: 10px;
    background: #fff;
}

.qr__container {
    display: flex;
    justify-content: center;
}

.qr__link {
    position: absolute;
    left: -9999px;
    top: -9999px;
}
