.c-input {
	display: inline-block;
	position: relative;
	vertical-align: top;
	width: 100%;
}

/*hide shadow in ios*/
/*hide arrow*/

.c-input__text {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
}

.c-input__text {
	padding: 8px 15px;
	border: 1px solid #ddd;
	border-radius: 4px;
	font-size: 16px;
	outline: none;
	font-weight: 300;
	box-sizing: border-box;
	margin: 5px 0;
	width: 100%;
	font-family: inherit;
}

.c-input__text:disabled {
	background: #f5f5f5;
}

.theme-dark .c-input__text:disabled {
	background: #333;
	color: #888;
} 

textarea.c-input__text {
	resize: vertical;
	min-height: 100px;
}

.c-input__text:placeholder {
	font-weight: 300;
}

.c-input__text:focus {
	border: 1px solid #262626;
}

.c-input__placeholder {
	color: #262626;	
    position: absolute;
    left: 15px;
    top: 0px;
    font-size: 14px;
    padding: 0 3px;
    background: #fff;
    margin-left: -2px;
    transition: 0.15s;
    opacity: 0;
    border-radius: 2px;
    font-weight: 300;
    color: #888;
    z-index: 2;
    pointer-events: none;
}

.c-input__text:focus + .c-input__placeholder,
.c-input__placeholder_fixed {
	top: -3px;
	opacity: 1;
	color: #262626;
}

.c-input__placeholder_fixed {
	color: #888;
}

.c-input--2 {
	width: calc( 50% - 4px );
}

.c-input--2 + .c-input--2,
.c-input--3 + .c-input--3 {
	margin-left: 8px;
} {
	margin-left: 8px;
}

.c-input--3 {
	width: calc( 33% - 5px );
}

.c-input--link:before {
	content: attr( data-link );
	display: inline-block;
	position: absolute;
	top: 14px;
	left: 16px;
	color: #333;
	font-weight: 300;
	z-index: 1;
}

.c-input__text--link {
	position: relative;
	z-index: 2;
	background: none;
}


.c-input--with-icon {
	padding: 0px 20px;
	box-sizing: border-box;
}

.c-input--with-icon-nopad {
	padding: 0;
	padding-left: 20px;
}

.c-input--with-icon-nopad-left {
    padding-left: 0;
}

.c-input--with-icon .c-input__icon {
    display: inline-block;
    vertical-align: top;
    padding-top: 8px;
    font-size: 26px;
    line-height: 30px;
    margin-right: 15px;
    width: 30px;
    text-align: center;
}

.c-input--with-icon .c-input__text,
.c-input--with-icon .c-select__select {
	display: inline-block;
    vertical-align: middle;
    box-sizing: border-box;
    width: calc( 100% - 50px );
}

.c-input--with-icon .c-input__placeholder {
	left: 85px;
}

.c-input--with-icon .c-input__text + .c-input__placeholder {
	
}

.c-input--with-icon + .c-input__note {
	padding-left: 70px;
}


.c-input__text--noborder {
	border: none;
	border-radius: 0;
	border-bottom: 1px solid #ddd;
}

.c-input__text--noborder:focus {
	border: none;
	border-bottom: 1px solid #000;
}

.c-input--with-icon-nopad-left .c-input__icon {
	margin-right: 5px;
}

.c-input--with-icon-nopad-left .c-input__text,
.c-input--with-icon-nopad-left .c-select__select {
	width: calc( 100% - 40px );
}


.c-input--with-icon-nopad-left .c-input__placeholder {
	left: 54px;
}

.theme-dark .c-input__text {
    background: #262626;
    border: 1px solid #555;
    color: inherit;
}

.theme-dark .c-input__text:focus,
.theme-dark .c-select__select:focus {
    border: 1px solid #fff;
}

.theme-dark .c-input__placeholder {
    background: #262626;
    color: #fff !important;
}